body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

section {
  padding: 50px;
}

.input {
    background-color: #383838;
    border: 1ex solid none;
    border-top-width: 0.2em;
    margin: 0;
    padding: 0;
    color: #383838;
    word-wrap: break-word;
    outline: 7px solid #383838;
    height: 30px;
    font-size: 17px;
    text-align: center;
    transition: all 1s;
    max-width: 190px;
    font-weight: bold;
    font-family: 'Courier New', Courier, monospace;
}

.input:focus {
    border-top-width: 0.2em;
    background-color: #f1e8e8;
}

.input-form {
    width: 100%;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    grid-gap: 30px;
    gap: 30px;
}
.loader {
    display: inline-block;
    position: relative;
    width: 80px;
    height: 80px;
    -webkit-transform: rotate(45deg);
            transform: rotate(45deg);
    -webkit-transform-origin: 40px 40px;
            transform-origin: 40px 40px;
   }
   
   .loader div {
    top: 32px;
    left: 32px;
    position: absolute;
    width: 32px;
    height: 32px;
    background: red;
    -webkit-animation: loader45 1.2s infinite cubic-bezier(0.215, 0.61, 0.355, 1);
            animation: loader45 1.2s infinite cubic-bezier(0.215, 0.61, 0.355, 1);
   }
   
   .loader div:after,
   .loader div:before {
    content: " ";
    position: absolute;
    display: block;
    width: 32px;
    height: 32px;
    background: red;
   }
   
   .loader div:before {
    left: -24px;
    border-radius: 50% 0 0 50%;
   }
   
   .loader div:after {
    top: -24px;
    border-radius: 50% 50% 0 0;
   }
   
   @-webkit-keyframes loader45 {
    0% {
     -webkit-transform: scale(0.95);
             transform: scale(0.95);
    }
   
    5% {
     -webkit-transform: scale(1.1);
             transform: scale(1.1);
    }
   
    39% {
     -webkit-transform: scale(0.85);
             transform: scale(0.85);
    }
   
    45% {
     -webkit-transform: scale(1);
             transform: scale(1);
    }
   
    60% {
     -webkit-transform: scale(0.95);
             transform: scale(0.95);
    }
   
    100% {
     -webkit-transform: scale(0.9);
             transform: scale(0.9);
    }
   }
   
   @keyframes loader45 {
    0% {
     -webkit-transform: scale(0.95);
             transform: scale(0.95);
    }
   
    5% {
     -webkit-transform: scale(1.1);
             transform: scale(1.1);
    }
   
    39% {
     -webkit-transform: scale(0.85);
             transform: scale(0.85);
    }
   
    45% {
     -webkit-transform: scale(1);
             transform: scale(1);
    }
   
    60% {
     -webkit-transform: scale(0.95);
             transform: scale(0.95);
    }
   
    100% {
     -webkit-transform: scale(0.9);
             transform: scale(0.9);
    }
   }
.container {
    width: 100%;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #B4E4FF;
}
.mainContent {
    background-color: #ffffff20;
    backdrop-filter: blue(12px);
    -webkit-backdrop-filter: blur(12px);
    height: 80vh;
    width: 70%;
    color: #fff;
    position: relative;
    border-radius: 20px;
    display: flex;
    overflow: hidden;
    justify-content: center;
    align-items: center;
    /* padding: 12px; */
}

.card__content {
    display: flex;
    overflow: hidden;
    justify-content: center;
    align-items: top;
    height: 100%;
    margin: 30px;
}

.card {
    width: 100%;
    height: 100%;
    margin: 0 auto;
    background-color: #011522;
    border-radius: 8px;
    z-index: 1;
}

.tools {
    display: flex;
    align-items: center;
    padding: 9px;
}

.circle {
    padding: 0 4px;
}

.box {
    display: inline-block;
    align-items: center;
    width: 10px;
    height: 10px;
    padding: 1px;
    border-radius: 50%;
}

.red {
    background-color: #ff605c;
}

.yellow {
    background-color: #ffbd44;
}

.green {
    background-color: #00ca4e;
}


.page {
    position: relative;
    box-sizing: border-box;
    width: 100%;
    height: 80%;
    font-family: cursive;
    font-size: 20px;
    border-radius: 10px;
    background: #fff;
    background-image: linear-gradient(#f5f5f0 1.1rem, #ccc 1.2rem);
    background-size: 100% 1.2rem;
    line-height: 1.2rem;
    padding: 1.4rem 0.5rem 0.3rem 4.5rem;
}

.page::before,
.page::after {
    position: absolute;
    content: "";
    bottom: 10px;
    width: 40%;
    height: 10px;
    box-shadow: 0 5px 14px rgba(0, 0, 0, 0.7);
    z-index: -1;
    transition: all 0.3s ease;
}

.page::before {
    left: 15px;
    -webkit-transform: skew(-5deg) rotate(-5deg);
            transform: skew(-5deg) rotate(-5deg);
}

.page::after {
    right: 15px;
    -webkit-transform: skew(5deg) rotate(5deg);
            transform: skew(5deg) rotate(5deg);
}

.page:hover::before,
.page:hover::after {
    box-shadow: 0 2px 14px rgba(0, 0, 0, 0.4);
}

.margin {
    position: absolute;
    border-left: 1px solid #d88;
    height: 100%;
    left: 3.3rem;
    top: 0;
}

.page p {
    margin: 0;
    /* text-indent: 1rem; */
    padding-bottom: 1.2rem;
    color: black;
    line-height: 20px;
}

